import React, { useState } from "react";
import PasswordBox from "../PasswordBox/PasswordBox";
import { sendRequestWithBody } from "../requests";
import "./QuizAdmin.css";

const QuizAdmin = () => {
  //Determines Whether To show The Password Box Or Not
  const [passView, setPassView] = useState(() => true); //Set it to True After testing phase
  //Keeps User Uptodate With Errors Or Successes With Actions The User Is Trying To Do
  const [status, setStatus] = useState(() => "Enter Admin Password To Proceed");
  //Stores the Text Inside The Search Bar
  const [searchText, setSearchText] = useState(() => "");
  //Stores The Password Stored In Password Box, On Right Password The Password Is Stored Here
  const [pass, setPass] = useState(() => ""); //Set To undefined after testing Phase
  //Stores Data Retreived By A Succesful Search By Id.
  const [data, setData] = useState(() => {
    return {
      title: "",
      _id: "",
      password: "",
    };
  });

  const handlePassSubmit = (textPass) => {
    sendRequestWithBody("api/admin", "POST", {
      password: textPass,
      _method: "check",
    }).then((msg) => {
      if (msg) {
        if (msg.err) {
          setStatus(msg.err);
        } else if (msg.msg) {
          setStatus("Welcome Admin !");
          setPassView(false);
          setPass(textPass);
        }
      } else {
        setStatus("Network Error");
      }
    });
  };

  const handleSearch = (textId) => {
    setData({ title: "", _id: "", password: "" }); //Reset Data On Search
    sendRequestWithBody("api/admin", "POST", {
      password: pass,
      _method: "show",
      _id: textId,
    }).then((info) => {
      if (info) {
        if (info.err) {
          setStatus(info.err);
        } else {
          setStatus("Data Loaded Succesfully");
          setData(info);
        }
      } else {
        setStatus("Network Error");
      }
    });
  };

  const handleDelete = () => {
    sendRequestWithBody("api/admin", "DELETE", {
      password: pass,
      _id: data._id,
    }).then((msg) => {
      if (msg) {
        if (msg.msg) {
          setStatus(msg.msg);
          setData({
            title: "",
            _id: "",
            password: "",
          });
        } else {
          setStatus(msg.err);
        }
      } else {
        setStatus("Network Error");
      }
    });
  };

  if (passView) {
    return <PasswordBox status={status} handleSubmit={handlePassSubmit} />;
  } else {
    return (
      <div className="quiz-admin-bg">
        <div className="quiz-admin-cont">
          <h1 className="quiz-admin-title">Admin Panel</h1>
          <input
            type="text"
            className="quiz-admin-search"
            placeholder="Search By ID"
            value={searchText}
            onChange={(e) => setSearchText(e.target.value)}
          />
          <button
            className="quiz-admin-btn"
            style={{ width: "10em" }}
            onClick={() => handleSearch(searchText)}
          >
            Seacrh
          </button>
          <div className="quiz-admin-info">
            <h3>Quiz-ID: </h3>
            <p>{data._id}</p>
          </div>
          <div className="quiz-admin-info">
            <h3>Quiz-Title: </h3>
            <p>{data.title}</p>
          </div>
          <div className="quiz-admin-info">
            <h3>Quiz-Password: </h3>
            <p>{data.password}</p>
          </div>
          <p className="quiz-admin-status">{status}</p>
          <button className="quiz-admin-btn" onClick={handleDelete}>
            Delete
          </button>
        </div>
      </div>
    );
  }
};

export default QuizAdmin;
