import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import QuizInfo from "../QuizInfo/QuizInfo";
import BadPage from "../BadPage/BadPage";
import QuizScore from "../QuizScore/QuizScore";
import { getOne } from "../requests";
import "./QuizPlay.css";

//contains a request to api
//Layout must not chnage otherwise this won't function
//Classes in Must Not Change Too, if Changed Change There defination here too.

const QuizPlay = ({ history, match }) => {
  /*---Helper Functions Start Here---*/

  useEffect(() => {
    if (match.params.id) {
      getOne(match.params.id).then((data) => {
        if (data) {
          if (data.err) {
            setFetchStatus(`${match.params.id} is not a vaild id`);
          } else {
            console.log(data);
            setQuizInfo(data);
          }
        } else {
          setFetchStatus(
            "Network Error, Please Make Sure You Have An Internet Connection."
          );
        }
      });
    }
  }, []);

  const resetOptionsClass = () => {
    //Resets Options To Initial State Must Be Called Whenever Changing The Question.
    const children = document.querySelector(".quiz-play-options").children;
    Array.prototype.forEach.call(children, (elem) => {
      elem.classList.remove("quiz-play-option-right");
      elem.classList.remove("quiz-play-option-wrong");
    });
  };

  const changeQuestion = (max) => {
    setQuestionNo((prevQuestionNo) => {
      if (prevQuestionNo < max - 1) {
        resetOptionsClass();
        return prevQuestionNo + 1;
      } else {
        setScoreView(() => true);
        return prevQuestionNo;
      }
    });
  };

  const checkOption = (target) => {
    const curQuestion = [...quizInfo.questions[questionNo]]; //Current Question No
    curQuestion.splice(0, 1); //Remove Question From Array, Leaving us with only options

    let isChecked = false;
    //Getting All Option Boxes
    let children = undefined;
    if (target.tagName === "P") {
      children = target.parentElement.children;
    } else {
      children = target.children;
    }

    //To prevent selecting from more than one options
    Array.prototype.some.call(children, (elem) => {
      if (elem.classList.contains("quiz-play-option-right")) {
        isChecked = true;
      } else if (elem.classList.contains("quiz-play-option-wrong")) {
        isChecked = true;
      }
    });

    const curOption = curQuestion.find((item) => item[0] === target.innerHTML);

    //Will only work if options are p elements
    //checks if we are clicking an option

    if (target.tagName === "P" && !isChecked) {
      //If The Option Being Clicked Is Right
      if (curOption) {
        if (curOption[1]) {
          setScore((prevScore) => prevScore + 1); //Increment Score By 1
          target.classList.add("quiz-play-option-right");
        } else {
          //If option being clicked is wrong
          target.classList.add("quiz-play-option-wrong");
        }
      }
      setOptionDone(true);
    }
  };

  /*---States---*/

  const [quizInfo, setQuizInfo] = useState(() => undefined);
  const [introView, setIntroView] = useState(() => true);
  const [scoreView, setScoreView] = useState(() => false);
  const [questionNo, setQuestionNo] = useState(() => 0);
  const [score, setScore] = useState(() => 0);
  const [optionDone, setOptionDone] = useState(() => false);
  const [fetchStatus, setFetchStatus] = useState(() => "Loading...");

  /*---Views---*/

  if (quizInfo) {
    if (introView) {
      return (
        <QuizInfo
          quizInfo={quizInfo}
          handleStartClick={() => {
            setIntroView(() => false);
          }}
          history={history}
        />
      );
    } else if (scoreView) {
      return (
        <QuizScore
          title={quizInfo.title}
          score={score}
          questionNo={quizInfo.questions.length}
          handleReplayClick={() => {
            setScoreView(false); //Vanish The ScoreView
            setScore(0); //Set The Score To 0
            setQuestionNo(0); //Start From The First Question
          }}
        />
      );
    } else {
      return (
        <div className="quiz-play-cont doodles-bg">
          <div className="quiz-play">
            <p className="quiz-play-question">
              {quizInfo.questions[questionNo][0]}
            </p>
            <div
              className="quiz-play-options"
              onClick={(e) => checkOption(e.target)}
            >
              {quizInfo.questions[questionNo].map((option, index, arr) => {
                if (index > 0 && index < arr.length) {
                  return (
                    <p className="quiz-play-option" key={index}>
                      {option}
                    </p>
                  );
                }
                return null;
                //we want to avoid index 0 becuase it is the question so 0 is equal to false so it won't render the question only options
              })}
            </div>
            <div className="quiz-play-footer">
              <Link to="/board">
                <button className="quiz-play-footer-btn">Cancel</button>
              </Link>

              <p className="quiz-play-footer-text">
                Question {questionNo + 1} out of {quizInfo.questions.length}
              </p>
              <button
                className="quiz-play-footer-btn"
                onClick={() => {
                  if (optionDone) {
                    changeQuestion(quizInfo.questions.length);
                    setOptionDone(false);
                  }
                }}
              >
                Next
              </button>
            </div>
          </div>
        </div>
      );
    }
  } else {
    return <BadPage message={fetchStatus} />;
  }
};

export default QuizPlay;
