import React, { useState, useEffect } from "react";
import { Switch, Route } from "react-router-dom";
import "./App.css";
import Home from "../Home/Home";
import QuizBoard from "../QuizBoard/QuizBoard";
import QuizPlay from "../QuizPlay/QuizPlay";
import QuizCreate from "../QuizCreate/QuizCreate";
import QuizAdmin from "../QuizAdmin/QuizAdmin";
import { getAllBasicData } from "../requests";

//Finally Implement PDF Generation And Downloading

function App() {
  //First element of questions array is question, rest are options

  return (
    <div className="App">
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/board" component={QuizBoard} />
        <Route path="/create" render={(props) => <QuizCreate {...props} />} />
        <Route path="/admin" component={QuizAdmin} />
        <Route path="/:id" render={(props) => <QuizPlay {...props} />} />
      </Switch>
    </div>
  );
}

export default App;
