import React from "react";
import { Link } from "react-router-dom";
import AlertBox from "../AlertBox/AlertBox";
import "./QuizCreateInfo.css";

//Give Default Values When Editing

const QuizCreateInfo = (props) => {
  //Initialize The Information as in create an object with given details
  const initInfo = (event) => {
    event.preventDefault(); //Prevent From Reloading The Page
    const data = new FormData(event.target); //Get Data From The Form
    let quizData = undefined;
    if (props.qData._id) {
      //WE Look at ID to determine if this object is full
      //Below Are Important Otherwise Update Won't Take Place
      props.qData.password = data.get("password");
      props.qData.title = data.get("title");
      props.qData.author = data.get("author");
      quizData = props.qData;
    } else {
      quizData = {
        title: data.get("title"),
        author: data.get("author"),
        password: data.get("password"),
        plays: 0,
        questions: [["Question 1", ["Yes", true], ["No", false]]],
      };
    }
    //Update QuizData With This Quiz
    props.setQuizData(quizData);
    props.setOptions(quizData.questions[0]); //You need this otherwise you will get an error that options is undefined
    //And Then Vanish This Page
    props.disappear(false);
  };

  return (
    <>
      <AlertBox />
      <div className="quiz-create-info-bg doodles-bg">
        <form className="quiz-create-info-cont" onSubmit={initInfo}>
          <h1 className="quiz-create-info-title">QUIZER</h1>
          <div className="quiz-create-info-label-cont">
            <label className="quiz-create-info-label">
              <p>Title</p>
              <input
                name="title"
                type="text"
                placeholder="Quiz Title"
                autoComplete="off"
                required={true}
                maxLength="50"
                defaultValue={props.qData.title}
              ></input>
            </label>
            <label className="quiz-create-info-label">
              <p>Name</p>
              <input
                name="author"
                type="text"
                placeholder="Your Name"
                autoComplete="off"
                required={true}
                maxLength="30"
                defaultValue={props.qData.author}
              ></input>
            </label>
            <label className="quiz-create-info-label">
              <p>Password</p>
              <input
                name="password"
                type="text"
                placeholder="Quiz Password"
                autoComplete="off"
                required={true}
                minLength="4"
                maxLength="10"
              ></input>
            </label>
            <div className="quiz-create-info-btns">
              <button type="reset" className="quiz-create-info-btn">
                Reset
              </button>
              <Link to="/">
                <button type="reset" className="quiz-create-info-btn">
                  Cancel
                </button>
              </Link>
              <button type="submit" className="quiz-create-info-btn">
                Create
              </button>
            </div>
          </div>
        </form>
      </div>
    </>
  );
};

export default QuizCreateInfo;
