import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import QuizCard from "../QuizCard/QuizCard";
import NavBar from "../Navbar/Navbar";
import { getAllBasicData } from "../requests";
import "./QuizBoard.css";

//If Data is not yet loaded Show Loading anime after Loading display data
//Show Loading And Error If Data Doesn't Load

const QuizBoard = () => {
  const [theme, setTheme] = useState(() => "blue");
  //Toggles The Theme
  const toggleTheme = () => {
    setTheme((prevTheme) => {
      if (prevTheme === "blue") {
        return "white";
      } else {
        return "blue";
      }
    });
  };

  const [quizesData, setQuizesData] = useState(() => []); //Loaded Data is Stored Here
  const [loading, setLoading] = useState(() => "Loading..."); //For Updating User With Current Loading Status
  const [pageNum, setPageNum] = useState(() => 0); //For Pagination
  const [finished, setFinished] = useState(() => false); //To Know When We Have Loaded All Data On The Server
  const [searchText, setSearchText] = useState(() => ""); //Controls Value Of Search Field in Navbar
  const [backupData, setBackupData] = useState(() => undefined); //Is Needed When Switching Between Search and Actual Data
  const [searchData, setSearchData] = useState(() => undefined); //For Storing Search Data

  useEffect(() => {
    //Fetch New Data And Then Append It To Existing Data
    if (!finished) {
      setLoading("Loading...");
      getAllBasicData(pageNum).then((res) => {
        if (res) {
          if (res.msg) {
            setFinished(true);
            setLoading("");
          } else {
            setQuizesData((prevData) => [...prevData, ...res]);
            setLoading("");
          }
        } else {
          setLoading(
            () =>
              "Network Error, Please Check Your Internet Connection And Refresh The Page"
          );
        }
      });
    }
  }, [pageNum]);

  //Responsible For Handling Search Results
  useEffect(() => {
    if (searchData) {
      //If we have fetched Some Search Results
      if (!backupData) {
        //If backupData stores nothing then append quizesData, this way we only backup original data
        setBackupData(quizesData); //Backup Original Data
      }
      setQuizesData(searchData); //Replace With Search Results
    }
  }, [searchData]);

  //Responsible For Retreiveing Original Backed Up Data When Search Field Becomes Empty
  useEffect(() => {
    //If Search Field Is Empty
    if (!searchText) {
      //If BackupData Stores Some data, important cause we can't set quizesData to Empty
      if (backupData) {
        setQuizesData(backupData); //Restore Orignial Data
        setBackupData(undefined); //Reset Backup Data
        setSearchData(undefined); //Reset Search Results
      }
    }
  }, [searchText]);

  const handleScroll = (e) => {
    //Loads New Data As We Hit The Bottom Of The Page
    if (!finished) {
      const { scrollHeight, scrollTop, clientHeight } = e.target;

      if (scrollHeight - clientHeight === scrollTop) {
        setPageNum((prev) => prev + 1);
      }
    }
  };

  let loadWrapper = undefined; //Determines Whether To Display The Message Or Loading Gif

  if (loading === "Loading...") {
    //Change This according to loading
    loadWrapper = (
      <img
        className="quiz-board-loading-gif"
        src={require("../../images/loader.gif")}
        alt="Loading..."
      />
    );
  } else {
    loadWrapper = <div className="quiz-board-loading">{loading}</div>;
  }

  return (
    <div className="quiz-board">
      <NavBar
        changeTheme={toggleTheme}
        setSearchData={setSearchData}
        searchText={searchText}
        setSearchText={setSearchText}
        setQuizesData={setQuizesData}
      />
      <div className={`quiz-cards ${theme}-quiz-cards`} onScroll={handleScroll}>
        {quizesData && //We Make Sure Data Exists Then We Display Data
          quizesData.map((info) => {
            return (
              <Link to={`/${info._id}`} key={info._id}>
                <QuizCard
                  title={info.title}
                  questions={info.questions.length}
                  views={info.views}
                  author={info.author}
                />
              </Link>
            );
          })}
        {loadWrapper}
      </div>
    </div>
  );
};

export default QuizBoard;
