import React from "react";
import { Link } from "react-router-dom";
import { searchFor } from "../requests";
import "./Navbar.css";

const Navbar = (props) => {
  const handleSearch = () => {
    //Fetches Searched data, uses no pagination
    searchFor(props.searchText).then((data) => {
      if (data) {
        if (!(data.msg || data.err)) {
          props.setSearchData(data);
        }
      }
    });
  };

  return (
    <div className="navbar">
      <Link to="/">
        <h1 className="navbar-logo">Quizer</h1>
      </Link>
      <div className="navbar-search">
        <input
          className="navbar-search-field"
          placeholder="Find A Quiz..."
          value={props.searchText}
          onChange={(e) => props.setSearchText(e.target.value)}
        />
        <img
          className="navbar-search-icon"
          src={require("./icons/search.svg")}
          alt="Search"
          onClick={handleSearch}
        />
      </div>
      <div className="navbar-options">
        <img
          className="navbar-options-icon"
          src={require("./icons/eye.svg")}
          alt="Change Theme"
          onClick={props.changeTheme}
        />
        <Link to="/create">
          <img
            className="navbar-options-icon"
            src={require("./icons/add.svg")}
            alt="Add A Quiz"
          />
        </Link>
        {/* Don't Need This Now So We Don't Render It <img
          className="navbar-options-icon"
          src={require("./icons/settings.svg")}
          alt="Settings"
        />*/}
      </div>
    </div>
  );
};

export default Navbar;
