import React from "react";
import "./QuizCard.css";

const QuizCard = (props) => {
  return (
    <div className="quiz-card">
      <h1 className="quiz-card-title-image">{props.title[0]}</h1>
      <div className="quiz-card-info">
        <p className="quiz-card-info-main">{props.title}</p>
        <p className="quiz-card-info-sub">Questions: {props.questions}</p>
        <p className="quiz-card-info-sub">views: {props.views}</p>
        <p className="quiz-card-info-sub">Author: {props.author}</p>
      </div>
    </div>
  );
};

export default QuizCard;
