import React from "react";
import { Link } from "react-router-dom";
import "./Home.css";

const Home = () => {
  return (
    <div className="home-container scales-bg">
      <div className="home-header">
        <h1 className="home-header-logo">Q</h1>
      </div>
      <div className="home-highlights">
        <div className="home-highlights-heading">
          <h1 className="home-highlights-heading-text">Quizer</h1>
        </div>
        <Link to="/board" className="home-highlights-subheading">
          Explore Quizzes
        </Link>
        <Link to="/create" className="home-highlights-subheading">
          Create A Quiz
        </Link>
      </div>
    </div>
  );
};

export default Home;
