import React from "react";
import "./QuizCreateOptionBox.css";

const QuizCreateOptionBox = (props) => {
  return (
    <div className="quiz-create-option-box-cont">
      <textarea
        maxLength="80"
        className="quiz-create-textarea"
        placeholder="Type An Option"
        spellCheck="false"
        value={props.text}
        onChange={(e) => props.handleInput(props.index, e.target.value, false)}
      ></textarea>
      <div>
        <button
          type="button"
          onClick={() =>
            props.handleRight(props.isRight ? false : true, props.index)
          }
          className={`quiz-create-option-box-cont-btn ${
            props.isRight ? "quiz-create-option-box-right" : ""
          }`}
        >
          Right
        </button>
        <button
          type="button"
          onClick={() => {
            if (props.arrLen > 3) {
              props.handleDelete(props.index);
            } else {
              props.setMsg("You Need To Have At Least Two Options.");
            }
          }}
          className={"quiz-create-option-box-cont-btn"}
        >
          Delete
        </button>
      </div>
    </div>
  );
};

export default QuizCreateOptionBox;
