import React from "react";
import { Link } from "react-router-dom";
import "./QuizScore.css";

const QuizScore = (props) => {
  return (
    <div className="quiz-score-bg doodles-bg">
      <div className="quiz-score-cont">
        <h1 className="quiz-score-title">{props.title}</h1>
        <div className="quiz-score-boxes">
          <div className="quiz-score-box">
            <p className="quiz-score-box-text">Total</p>
            <p className="quiz-score-box-num">{props.questionNo}</p>
          </div>
          <div className="quiz-score-box">
            <p className="quiz-score-box-text">Right</p>
            <p className="quiz-score-box-num">{props.score}</p>
          </div>
          <div className="quiz-score-box">
            <p className="quiz-score-box-text">Wrong</p>
            <p className="quiz-score-box-num">
              {props.questionNo - props.score}
            </p>
          </div>
          <div className="quiz-score-box">
            <p className="quiz-score-box-text">Percentage</p>
            <p className="quiz-score-box-num">
              {Math.floor((props.score / props.questionNo) * 100) + "%"}
            </p>
          </div>
        </div>
        <div className="quiz-score-btns">
          <Link to="/board">
            <button type="button" className="quiz-score-btn">
              More Quizzes
            </button>
          </Link>
          <button
            type="button"
            className="quiz-score-btn"
            onClick={props.handleReplayClick}
          >
            Replay
          </button>
        </div>
      </div>
    </div>
  );
};

export default QuizScore;
