export const randomizeOptions = (arr) => {
  const newArr = [...arr]; //Create A New Array Without Question
  newArr.splice(0, 1);
  const randArr = []; //Array For Randomized Option
  const newArrLen = newArr.length;

  for (let i = 0; i < newArrLen; i++) {
    let index = Math.floor(Math.random() * newArr.length); //Randomly Generated Index
    randArr.push(newArr[index]); //Push New Selected Option Into randArr
    newArr.splice(index, 1); //Remove That Option From Options Array
  }

  randArr.unshift(arr[0]); //Add The Question Into Randomize Array

  return randArr; //Return Newly Created Array
};

export const replaceQuestion = (quInd, opArr, obj) => {
  const newObj = { ...obj };
  obj.questions.splice(quInd, 1, opArr);
  return newObj;
};

export const updateArrVal = (arr, ind, val, op = "replace") => {
  const arrCopy = [...arr];
  //console.log(val);
  switch (op) {
    case "replace":
      arrCopy.splice(ind, 1, val);
      break;
    case "remove":
      arrCopy.splice(ind, 1);
      break;
    case "add":
      if (val) {
        arrCopy.splice(ind, 0, val);
      }
      break;
    default:
      console.log("No Valid Operation Provided To Perform");
      break;
  }

  return arrCopy;
};

export const updateOptionArr = (val, arr) => {
  const arrCopy = [...arr];
  if (typeof val === "boolean") {
    arrCopy.pop();
    arrCopy.push(val);
  } else {
    arrCopy.shift();
    arrCopy.unshift(val);
  }
  return arrCopy;
};
