import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import QuizCreateInfo from "../QuizCreateInfo/QuizCreateInfo";
import QuizCreateOptionBox from "../QuizCreateOptionBox/QuizCreateOptionBox";
import AlertBox from "../AlertBox/AlertBox";
import PasswordBox from "../PasswordBox/PasswordBox";
import {
  replaceQuestion,
  randomizeOptions,
  updateArrVal,
  updateOptionArr,
} from "../tools";
import { patchOne, postOne } from "../requests";
import "./QuizCreate.css";

//Something Wrong When Composing Body We Missout Password hence setting the password in database to null

const QuizCreate = (props) => {
  //Determines To Show QuizCreateInfo Component
  const [InfoView, setInfoView] = useState(() => true);

  //It wiil be filled after infoView
  const [quizData, setQuizData] = useState(() => undefined);

  //The Question We Are On Now
  const [questionNo, setQuestionNo] = useState(() => 0);
  //Input For Adding The Option
  const [curOptionInput, setCurOptionInput] = useState(() => "");
  //Array Of Options Of Current Question
  const [options, setOptions] = useState(() => undefined); //quizData.questions[questionNo]);
  //Updates Options Array As The QuestionNo Changes To Display The Proper Options And Questions

  const [passView, setPassView] = useState(() => false); //For Asking For Password
  const [passMsg, setPassMsg] = useState(
    () => "Enter Password To Update This Quiz"
  );

  /*---Alert Box BoilerPlate ---*/
  const [alertMsg, setAlertMsg] = useState(() => ""); //Initialize with some Text To Display At The Start Of The App
  const [inAnime, setInAnime] = useState(() => Boolean(alertMsg)); //Initialize with true if Msg is not empty else with false
  const setMsg = (msg) => {
    //If we are not in between an animation already
    if (!inAnime) {
      //then allow to change the message
      setAlertMsg(msg);
    }
  };

  useEffect(() => {
    //If is important otherwise you will get an error that quizData is undefined
    if (!InfoView) {
      setOptions(quizData.questions[questionNo]);
    }
  }, [questionNo]);

  //Helps Update An Option in Options Array
  const handleOptionInput = (ind, val, str = true, arr = options) => {
    if (str) {
      //If String is given then update normally
      setOptions(updateArrVal(arr, ind, val, "replace"));
    } else {
      //Otherwise if it is an array update the first item of array and then update options
      setOptions(
        updateArrVal(arr, ind, updateOptionArr(val, options[ind]), "replace")
      );
    }
  };
  //Deletes An item at given index in options array
  const handleOptionDelete = (ind, arr = options) => {
    setOptions(updateArrVal(arr, ind, undefined, "remove"));
  };
  //Updates The Second Item of An Option In Options Array On Given Index
  const handleOptionRight = (val, ind, arr = options) => {
    setOptions(
      updateArrVal(options, ind, updateOptionArr(val, arr[ind]), "replace")
    );
  };

  //Updates The Current Question With Given Details
  const handleAddQuestion = (forSave = false) => {
    let newArr = [...options]; //Copy The options Array into this NewArr

    //Trim out whitespaces in options
    newArr = newArr.map((item, index) => {
      if (index > 0) {
        //If we are on an option
        return [item[0].trim(), item[1]]; //Trim whitespaces of the first item of option array
      } else {
        return item.trim(); //If we are on question just trim the white spaces
      }
    });

    //Checks For Empty Options
    let emptyOption = false;
    newArr = newArr.map((item, index) => {
      if (index > 0) {
        //If we are on an option
        if (!item[0]) {
          //First Item of option Array is empty
          emptyOption = true;
          return [`Option ${index}`, item[1]]; //Fill that option array with this data
        } else {
          return item; //otherwise if it is not empty return the same item
        }
      } else {
        //if we are on a question
        if (item) {
          //If Question is not empty
          return item; //Return the same question
        } else {
          //other wise fill the question with this data
          emptyOption = true;
          return `Question ${questionNo + 1}`;
        }
      }
    });

    if (!emptyOption) {
      //Determine is if at least one box is selected as right
      const rightSelected = newArr.some((item, index) => {
        if (index > 0) {
          //if we are on an option array
          return item[1]; //and at least one of the option array contain true as their second item
        }
        return false; //Added Not Tested
      });

      if (rightSelected) {
        //If At least one option is selected as right
        let duplicateOption = false; //initializing duplicateOption with false
        //Check For Duplicate Options
        for (let i = 1; i < newArr.length; i++) {
          for (let j = 1; j < newArr.length; j++) {
            if (i !== j && newArr[i][0] === newArr[j][0]) {
              //Add Alert Here For Duplicate Option
              //if two options have same text duplicateOption is set to true
              duplicateOption = true;
              break;
            }
          }
        }

        if (!duplicateOption) {
          //if there is no duplicate option as in with the same text
          //Checking For Duplicate Questions
          const duplicateQuestion = quizData.questions.some((item, index) => {
            if (index !== questionNo) {
              //if we are not comparing this question with this question
              return item[0] === newArr[0]; //only then compare this question with other questions
            } else {
              return false;
            }
          });

          if (!duplicateQuestion) {
            //If there is no duplicate question
            newArr = randomizeOptions(newArr); //Randomize Options
            setQuizData(replaceQuestion(questionNo, newArr, quizData)); //Update Question In Quiz Data
            //If we are saving we will not create a new question we validate this question and return true
            if (forSave) {
              return true;
            }

            //If We Are At The Last Question Or No More Questions Exist
            if (questionNo + 1 >= quizData.questions.length) {
              //= sign would do it
              //Create A New Question With Default Values
              setQuizData({
                ...quizData,
                questions: [
                  ...quizData.questions,
                  [
                    `Question ${questionNo + 2}`,
                    ["Option 1", true],
                    ["Option 2", false],
                  ],
                ],
              });
            }
            //Move To Next Question
            setQuestionNo(questionNo + 1);
          }
        } else {
          setMsg("Cannot Update Question, Duplicate Options Are Not Allowed");
        }
      } else {
        setMsg(
          "Cannot Update Question, Atleast One Right Option Needs To Be Selected"
        );
      }
    } else {
      setOptions(newArr);
      setMsg("Cannot Update Question, One Of The Options Is Empty.");
    }
    return false;
  };

  //Deletes The Question From Current Quiz
  const handleDeleteQusetion = () => {
    //If we have at least 2 questions in this quiz
    if (quizData.questions.length >= 2) {
      if (questionNo === 0) {
        //if we are on the first question
        setQuestionNo(questionNo + 1); //Move to next question
      } else {
        //if we are not on the first question Move to previous question
        setQuestionNo(questionNo - 1);
      }
      //Copy questions array into newArr
      const newArr = [...quizData.questions];
      newArr.splice(questionNo, 1); //Delete The Question From Quiz
      setQuizData({ ...quizData, questions: newArr }); //Update The Quiz
    } else {
      setMsg(
        "Cannot Delete Question, A Quiz Must Contain At Least Two Questions."
      );
    }
  };

  //Saves This Quiz Into The Global Quizes
  const handleSaveQuiz = () => {
    //Check If At Least Two Questions Exist In The Quiz
    const newObj = { ...quizData }; //Copy quizData object in newObj
    postOne(newObj).then((data) => {
      if (data) {
        console.log(data);
        setQuizData(data); //Update This Quiz
        props.history.push("/board"); //Go To Board Page
      } else {
        setMsg(
          "Couldn't Save Quiz Because Of Network Error, Please Try Again. You Will Be Taken To Another Page When Quiz Succesfully Saves."
        );
      }
    });
  };

  const handleUpdateQuiz = (passText) => {
    const newObj = { ...quizData, _password: passText };
    patchOne(newObj).then((data) => {
      if (!data) {
        //If Data is not saved or something like that
        setPassMsg("Network Error, Check Your Internet Connection.");
      } else {
        if (data.err) {
          setPassMsg(data.err);
        } else {
          props.history.push("/board");
        }
      }
    });
  };

  if (InfoView) {
    //If we first come we will see QuizCreateInfo Component That Initializes Quiz Data
    return (
      <QuizCreateInfo
        setQuizData={setQuizData}
        setOptions={setOptions}
        disappear={setInfoView}
        qData={props.location.qData || { title: undefined, author: undefined }} //Second Object Is Important Otherwise it will cause an error
      />
    );
  } else {
    //After Initializing we see the actual QuizCreate Component
    return (
      <>
        <AlertBox message={alertMsg} onAnime={setInAnime} />
        {passView && (
          <PasswordBox
            handleCancel={() => setPassView(false)}
            handleSubmit={handleUpdateQuiz}
            status={passMsg}
          />
        )}
        <div className="quiz-create-cont tornado-bg">
          <div className="quiz-create">
            <textarea
              maxLength="100"
              className="quiz-create-question quiz-create-textarea"
              placeholder="Type Your Question"
              spellCheck="false"
              value={options[0]}
              onChange={(e) => {
                setOptions(updateArrVal(options, 0, e.target.value, "replace"));
              }}
            ></textarea>
            <div className="quiz-create-option-cont">
              <textarea
                maxLength="80"
                className="quiz-create-textarea"
                placeholder="Type An Option"
                spellCheck="false"
                value={curOptionInput}
                onChange={(e) => setCurOptionInput(e.target.value)}
              ></textarea>
              <button
                type="button"
                className="quiz-create-option-btn quiz-create-option-btn-large"
                onClick={() => {
                  //One Question And Six For Options
                  //Setting it to 9 limits to 7 options

                  const thisInput = curOptionInput.trim(); //Get Rid Of unneccessary white spaces

                  //Stop Processing If Input Is Empty
                  if (!thisInput) {
                    setMsg("Cannot Add An Empty Option");
                    return;
                  }

                  //If an option with same text already exists
                  const match = options.find((itemArr, index) => {
                    if (index > 0) {
                      return itemArr[0] === thisInput;
                    }
                    return false;
                  });

                  //If we have less than 7 options and there are no duplicate options

                  if (options.length < 7) {
                    if (!match) {
                      setOptions(
                        updateArrVal(options, 1, [thisInput, false], "add")
                      );
                      setCurOptionInput("");
                    } else {
                      setMsg(
                        "Cannot Add This Option, Such Option Already Exists"
                      );
                    }
                  } else {
                    setMsg("Cannot Add More Than 6 Options.");
                  }
                }}
              >
                Add Option
              </button>
            </div>
            {options.map((item, index, arr) => {
              //Start After First Element
              if (index > 0) {
                return (
                  <QuizCreateOptionBox
                    key={index}
                    index={index}
                    text={item[0]}
                    arrLen={arr.length}
                    handleInput={handleOptionInput}
                    handleDelete={handleOptionDelete}
                    isRight={arr[index][1]}
                    handleRight={handleOptionRight}
                    setMsg={setMsg}
                  />
                );
              }
              return null; //Added Not Tested
            })}
          </div>

          <div className="quiz-create-btns-cont">
            <button
              onClick={() => {
                if (questionNo - 1 >= 0) {
                  //if we are not on the last question, move to previous question
                  setQuestionNo(questionNo - 1);
                } else {
                  setMsg("There Are No More Questions To Go Back To.");
                }
              }}
            >
              Previous
            </button>
            <Link to="/">
              <button>Cancel</button>
            </Link>
            <button
              onClick={() => {
                if (quizData.questions.length >= 2) {
                  if (handleAddQuestion(true)) {
                    if (!quizData._id) {
                      //Posts
                      //Newly created quizes don't have id so we use it to differentiate between updating and posting
                      handleSaveQuiz();
                    } else {
                      //Updates
                      setPassView(true);
                    }
                  } else {
                    setMsg(
                      "In Order To Save, You Must Correct Your Questions."
                    );
                  }
                } else {
                  setMsg(
                    "In Order To Save, You Must Create At Least Two Questions."
                  );
                }
              }}
            >
              Save Quiz
            </button>
            <button onClick={handleDeleteQusetion}>Delete Question</button>
            <button onClick={() => handleAddQuestion(false)}>
              Update Question
            </button>
          </div>
        </div>
      </>
    );
  }
};

export default QuizCreate;
