import { URL } from "./config";

export const getAllBasicData = async (pageNo, flag = "q-off") => {
  let data = undefined;
  try {
    const response = await fetch(URL + `api/all?page=${pageNo}&flag=${flag}`);
    data = await response.json();
  } catch (err) {
    return undefined;
  }

  return data;
};

export const getAllData = async () => {
  let data = undefined;
  try {
    const response = await fetch(URL + "api/all?flag=q-off");
    data = await response.json();
  } catch (err) {
    return undefined;
  }

  return data;
};

export const getOne = async (id, flag = "") => {
  let data = undefined;
  try {
    const response = await fetch(`${URL}api/one/${id}?flag=${flag}`);
    data = await response.json();
  } catch {
    return undefined;
  }
  return data;
};

export const postOne = async (body) => {
  let data = undefined;
  try {
    let postedData = await fetch(`${URL}api/add`, {
      method: "POST",
      headers: {
        Accept: "application/json, text/plain, */*",
        "Content-type": "application/json",
      },
      body: JSON.stringify(body),
    });

    data = await postedData.json();
  } catch (err) {
    console.log(err.message);
  }

  return data;
};

