export const sendRequestWithBody = async (url, method, body) => {
  let data = undefined;
  try {
    let receivedData = await fetch(`${URL}${url}`, {
      method: method,
      headers: {
        Accept: "application/json, text/plain, */*",
        "Content-type": "application/json",
      },
      body: JSON.stringify(body),
    });

    data = await receivedData.json();
  } catch (err) {
    data = err.message;
  }

  console.log(data);
  return data;
};

export const searchFor = async (toSearch, pageNum, flag = "q-off") => {
  let data = undefined;
  let page = "";
  if (pageNum >= 0) {
    page = `&page=${pageNum}`;
  }
  try {
    const response = await fetch(
      `${URL}api/search?q=${toSearch}&flag=${flag}${page}`
    );
    data = await response.json();
  } catch {
    return undefined;
  }
  return data;
};
