import React, { useState } from "react";
import { Link } from "react-router-dom";
import { PDFDownloadLink } from "@react-pdf/renderer";
import Doc from "../Doc/Doc";
import PasswordBox from "../PasswordBox/PasswordBox";
import { deleteOne } from "../requests";
import "./QuizInfo.css";

const QuizInfo = ({ quizInfo, handleStartClick, history }) => {
  const [passView, setPassView] = useState(() => false);
  const [passMsg, setPassMsg] = useState(() => "Enter Password To Delete");
  const handleDeleteQuiz = (textPass) => {
    deleteOne({ _id: quizInfo._id, _password: textPass }).then((msg) => {
      if (msg) {
        if (!msg.err) {
          setPassMsg("Quiz Deleted Succesfully");
          history.push("/board");
        } else {
          setPassMsg(msg.err);
        }
      } else {
        setPassMsg("Network Error, Please Check Your Internet Connection");
      }
    });
  };

  return (
    <>
      {passView && (
        <PasswordBox
          handleCancel={() => setPassView(false)}
          handleSubmit={handleDeleteQuiz}
          status={passMsg}
        />
      )}
      <div className="quiz-info doodles-bg">
        <p className="quiz-info-text-main">{quizInfo.title}</p>
        <div className="quiz-info-subs">
          <p className="quiz-info-text-sub">
            Qustions: {quizInfo.questions.length}
          </p>
          <p className="quiz-info-text-sub">Views: {quizInfo.views}</p>
          <p className="quiz-info-text-sub">Author: {quizInfo.author}</p>
          <p className="quiz-info-text-sub">
            {new Date(quizInfo.date).toLocaleString()}
          </p>
        </div>
        <div className="quiz-info-btns">
          <Link to="/board">
            <button type="button" className="quiz-info-btn">
              Go Back
            </button>
          </Link>
          <button
            type="button"
            onClick={() => setPassView(true)}
            className="quiz-info-btn"
          >
            Delete
          </button>
          <Link to={{ pathname: "/create", qData: quizInfo }}>
            <button
              type="button"
              className="quiz-info-btn"
              onClick={() => true}
            >
              Edit
            </button>
          </Link>
          <button
            type="button"
            className="quiz-info-btn"
            onClick={handleStartClick}
          >
            Start
          </button>
          <PDFDownloadLink
            document={<Doc data={quizInfo} />}
            fileName={"MyDoc.pdf"}
            className="quiz-info-btn"
            style={{
              textAlign: "center",
              lineHeight: "2.5em",
            }}
          >
            PDF
          </PDFDownloadLink>
        </div>
      </div>
    </>
  );
};

export default QuizInfo;
