import React, { useState } from "react";
import "./PasswordBox.css";

const PasswordBox = ({ status, handleCancel, handleSubmit }) => {
  const [show, setShow] = useState(() => false);
  const toggleShow = () => {
    if (show) {
      setShow(false);
    } else {
      setShow(true);
    }
  };

  return (
    <div className="quiz-pass-bg">
      <div className="quiz-pass-cont">
        <p>Enter Password</p>
        <input
          type={show ? "text" : "password"}
          placeholder="Password"
          id="password-box-input"
        />
        <p className="quiz-pass-status">
          {status || "Waiting For You To Submit..."}
        </p>
        <div>
          <button type="button" onClick={handleCancel}>
            Cancel
          </button>
          <button type="button" onClick={toggleShow}>
            Show
          </button>
          <button
            type="button"
            onClick={() =>
              handleSubmit(document.getElementById("password-box-input").value)
            }
          >
            Submit
          </button>
        </div>
      </div>
    </div>
  );
};

export default PasswordBox;
