import React from "react";
import { Document, Page, Text, View, StyleSheet } from "@react-pdf/renderer";

const alphabet = ["0", "a", "b", "c", "d", "e", "f"];

const Doc = ({ data }) => (
  <Document>
    <Page style={styles.body}>
      <Text style={styles.header} fixed>
        ~ Quizer ~
      </Text>
      <Text style={styles.title}>{data.title}</Text>
      <Text style={styles.author}>By {data.author}</Text>
      {data.questions.map((item, index, arr) => {
        return item.map((it, ind) => {
          if (ind === 0) {
            return (
              <Text style={styles.subtitle} key={ind}>{`${
                index + 1
              }. ${it}`}</Text>
            );
          } else {
            return (
              <Text
                style={styles.text}
                key={ind}
              >{`${alphabet[ind]}. ${it[0]}`}</Text>
            );
          }
        });
      })}
      <Text style={styles.answer}>Answers</Text>
      {data.questions.map((q, index, arr) => {
        return (
          <Text style={styles.text} key={index}>
            {index + 1 + ". "}
            {
              q
                .map((ans, ind) => {
                  if (ind !== 0) {
                    //If we are not on question
                    if (ans[1]) {
                      //If Answer we are checking is right
                      return alphabet[ind]; //Return its index
                    }
                  }
                })
                .filter((item) => item !== undefined) //Filter out undefined items
                .join(", ") //Convert array into a string so we can render it properly
            }
          </Text>
        );
      })}
    </Page>
  </Document>
);

const styles = StyleSheet.create({
  body: {
    paddingTop: 35,
    paddingBottom: 65,
    paddingHorizontal: 35,
  },
  title: {
    fontSize: 24,
    textAlign: "center",
    fontFamily: "Helvetica",
  },
  author: {
    fontSize: 12,
    textAlign: "center",
    marginBottom: 40,
  },
  subtitle: {
    fontSize: 18,
    margin: 15,
    marginLeft: 5,
    fontFamily: "Helvetica",
  },
  text: {
    marginTop: 10,
    marginLeft: 20,
    marginBottom: 5,
    fontSize: 14,
    textAlign: "justify",
    fontFamily: "Times-Roman",
  },
  image: {
    marginVertical: 15,
    marginHorizontal: 100,
  },
  header: {
    fontSize: 12,
    marginBottom: 20,
    textAlign: "center",
    color: "grey",
  },
  answer: {
    fontSize: 20,
    textAlign: "center",
    textDecoration: "underline",
    fontFamily: "Times-Roman",
    margin: 45,
  },
});

export default Doc;
