import React, { useEffect } from "react";
import "./AlertBox.css";

const AlertBox = ({ message, onAnime }) => {
  //this effect runs everytime message changes
  useEffect(() => {
    const alertElem = document.querySelector(".alert-box-text"); //Fetch The Alert Box Element
    if (message) {
      onAnime(true);
      alertElem.classList.add("fade"); //Add fade class to Alert Box
      alertElem.classList.remove("display-off"); //Add display-off to Alert Box
      const animeTime = 4000; //The Animation Duration
      setTimeout(() => {
        //After Animation Finishes
        alertElem.classList.add("display-off"); //Add display-off class to Alert Box
        onAnime(false); //
      }, animeTime);
      //console.log(message);
    }
    return () => {
      //console.log("Clean Up");
      //Before starting a new animation remove the fade class from Alert Box
      alertElem.classList.remove("fade");
    };
  }, [message]);

  return <h1 className="alert-box-text display-off">{message}</h1>;
};

export default AlertBox;
